<template>

  <section>

    <div class="leftdrop">

      <ul class="leftdrop-ul" >
        <li @click="meA">
          <router-link to="/index">
            我的主页
          </router-link>
        </li>
        <drop-one v-show="isShowA"></drop-one>
        <li @click="meB">
          <router-link to="/chart">
          商品列表
          </router-link>
        </li>
        <drop-two v-show="isShowB"></drop-two>
        <li @click="meC">
          <router-link to="/file">
          销售记录
          </router-link>
        </li>
        <drop-there v-show="isShowC"></drop-there>
        <li @click="meD">
          <router-link to="/page">
          应用
          </router-link>
        </li>
        <drop-four v-show="isShowD"></drop-four>
        <li @click="meE">
          <router-link to="/dash">
          高级
          </router-link>
        </li>
        <drop-five v-show="isShowE"></drop-five>
        <li @click="meF">
          <router-link to="/user">
          用户
          </router-link>
        </li>
        <drop-six v-show="isShowF"></drop-six>
        <li @click="meG">
          设置
        </li>
        <drop-seven v-show="isShowG"></drop-seven>
        <li >
          <router-link to="/tables">
            授权
          </router-link>


        </li>

      </ul>
    </div>
  </section>

</template>

<script>
  import DropOne from "../Leftdrop/DropOne";
  import DropTwo from "../Leftdrop/DropTwo";
  import DropThere from "../Leftdrop/DropThere";
  import DropFour from "../Leftdrop/DropFour";
  import DropFive from "../Leftdrop/DropFive";
  import DropSix from "../Leftdrop/DropSix";
  import DropSeven from "../Leftdrop/DropSeven";
  export default {
    name: "IndexLeft",
    components: { DropSeven, DropSix, DropFive, DropFour, DropThere, DropTwo, DropOne},
    data(){
      return{
        isShowA:false,
        isShowB:false,
        isShowC:false,
        isShowD:false,
        isShowE:false,
        isShowF:false,
        isShowG:false,
      }
    },
    methods:{
      meA(){
        this.isShowA=!this.isShowA;
      },
      meB(){
        this.isShowB=!this.isShowB;
      },
      meC(){
        this.isShowC=!this.isShowC;
      },
      meD(){
        this.isShowD=!this.isShowD;
      },
      meE(){
        this.isShowE=!this.isShowE;
      },
      meF(){
        this.isShowF=!this.isShowF;
      },
      meG(){
        this.isShowG=!this.isShowG;
      },
    }
  }
</script>

<style scoped>
  section{
    height: 100%;
  }
  .leftdrop {
    width: 250px;
    height: 100%;
    background-color: #151515;
    color: #959595;
    line-height: 60px;
    font-size: 18px;

  }

  .leftdrop h3 {
    text-align: center;
    height: 7%;
    color: white;
    border-bottom: 1px solid #666666;
  }

  .leftdrop-ul {
    height: 97%;
  }
  .leftdrop-ul li{
    padding-left: 20px;
    color: #fff;
  }
  .leftdrop-ul li a{
    display: block;
    color: #fff;
  }
 ul li:hover{
   border-left:5px solid deeppink;
   margin: 0;
   color: white;
   padding-left: 15px;
 }
</style>



