import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import Index from '@/pages/Index'
import Login from  '../pages/Login'
import Chart from  '../pages/Charts'
import File from  '../pages/FileManager'
import Page from  '../pages/PageLayout'
import Tables from  '../pages/Tables'
import Dash from  '../pages/DashBoard'
import User from  '../pages/Users'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/chart',
      name: 'Chart',
      component: Chart
    },
    {
      path: '/file',
      name: 'File',
      component: File
    },
    {
      path: '/page',
      name: 'Page',
      component: Page
    },
    {
      path: '/tables',
      name: 'Tables',
      component: Tables
    },
    {
      path: '/dash',
      name: 'Dash',
      component: Dash
    },
    {
      path: '/user',
      name: 'User',
      component: User
    }
  ]
})



