<template>

  <div class="box">
    <div class="section_left">
      <index-left></index-left>
    </div>
      <div class="section_right">
        <index-header></index-header>
        <div class="section_chart">
          <div class="two_top">
            <img src="../../static/images/4.jpg"/>线图
          </div>
          <div class="section_graphImg" id="graphImg_one">
          </div>
        </div>
        <div class="section_chart">
          <div class="two_top">
            <img src="../../static/images/16.jpg"/>条形图
          </div>
          <div class="section_graphImg" id="graphImg_two">
          </div>
        </div>
        <div class="section_pie">
          <div class="pie_left">
            <div class="two_top">
              <img src="../../static/images/16.jpg"/>饼图
            </div>
            <div class="pieLeft_section" id="pie_chartLeft">
            </div>
          </div>
          <div class="pie_right">
            <div class="two_top">
              <img src="../../static/images/16.jpg"/>饼图
            </div>
            <div class="pieRight_section" id="pie_chartRight">
            </div>
          </div>
        </div>
        <section-botton></section-botton>
      </div>
    </div>
</template>

<script>
    import IndexLeft from "../components/index/IndexLeft";
    import SectionBotton from "../components/index/SectionBotton";
    import IndexHeader from "../components/index/IndexHeader";

    // import {draw} from "../assets/js/charts.js"  不能用
    import charJs from "../assets/js/charts.js"
    import histJs from "../assets/js/histogram"
    import pieLeftJs from "../assets/js/pie_chart"
    import pieRightJs from "../assets/js/pie_chartpercent"

    export default {
        name: "Charts",
      components: {IndexHeader, SectionBotton, IndexLeft},
      mounted() {
        charJs.draw()
        histJs.hist()
        pieLeftJs.pieLeft()
        pieRightJs.pieRight()
      }
    }
</script>

<style scoped>
  .box{
    width: 100%;
    position: relative;
  }
  .section_left{
    width: 250px;
    float: left;
    height: 100%;
    position: absolute;
  }
  /*section*/
  .section_right{
    width: 100%;
    padding: 10px 45px 0 295px;
    box-sizing: border-box;
    background: url("../../static/images/mainface.png") no-repeat left top;
    background-size: cover;
  }
  .two_top {
    background-color: #3b3c3e;
    padding: 10px 25px;
    font-size: 16px;
    color: #aed254;
    border-radius: 5px;
    box-sizing: border-box;
  }
  .two_top img{
    width: 30px;
    height: 28px;
    vertical-align: middle;
  }

  .section_chart{
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .section_graphImg{
    padding: 25px;
    height: 265px;
    border:1px solid #bcbcbc;
    box-sizing: border-box;
    background-color: #f8f8f8;
  }
  .section_pie{
    /*margin-bottom: 40px;*/
    display: flex;
  }
  .pie_left{
    width: 50%;
    margin-right: 40px;
  }
  .pieLeft_section{
    border:1px solid #bcbcbc;
    padding: 25px;
    height: 265px;
    background-color: #f8f8f8;
  }
  .pie_right{
    width: 50%;
  }
  .pieRight_section{
    border:1px solid #bcbcbc;
    padding: 25px;
    height: 265px;
    background-color: #f8f8f8;
  }
  .pieRight_section img{
    width: 100%;
    height: 270px;
  }
  @media screen and (max-width: 690px){
    .section_pie{
      flex-direction: column;
    }
    .pie_left{
      width: 100%;
      margin-bottom: 40px;
    }
    .pie_right{
      width: 100%;
    }
  }
</style>



